@use "global" as *;

.js-scroll-top {
    display: block;
    width: 0;
    border-top: solid rem(25) rgba(255, 0, 0, 0);
    border-right: solid rem(25) rgba(255, 0, 0, 0);
    border-bottom: solid rem(25) rgba(255, 0, 0, 0);
    border-left: solid rem(25) rgba(255, 0, 0, 0);
    border-radius: rem(45);
    padding: 0;
    position: fixed;
    z-index: 10;
    bottom: rem(50);
    right: rem(50);
    cursor: pointer;
    transition: 0.3s linear;

    @include mq(md) {
        // 
    }
}

.js-scroll-top::before {
    content: "top";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgba(51, 51, 51, 0);
    transition: 0.3s linear;
}

// js-scroll-top active
.js-scroll-top-active {
    border-top: solid rem(25) rgba(255, 0, 0, 0.3);
    border-right: solid rem(25) rgba(255, 0, 0, 0.3);
    border-bottom: solid rem(25) rgba(255, 0, 0, 0.3);
    border-left: solid rem(25) rgba(255, 0, 0, 0.3);

}

.js-scroll-top-active::before {
    color: rgba(51, 51, 51, 0.3);

}