.js-drawer-menu {
  position: fixed;
  top: -100%;
  left: 0;
  z-index: 5;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100dvh;
  padding-top: 0rem;
  transition: 0.3s linear;
}
@media screen and (min-width: 768px) {
  .js-drawer-menu {
    display: none;
  }
}

.js-drawer-menu-open {
  top: 0;
}
.js-drawer-menu > ul {
  width: 60%;
  background: rgb(0, 0, 0);
  padding-left: 3.125rem;
  padding-right: 1.25rem;
  padding-top: 10.625rem;
  height: 100dvh;
  margin-left: auto;
}
.js-drawer-menu > ul > li > a > img {
  width: 1rem;
  margin-right: 0.625rem;
}

.js-drawer-menu > ul > li > a {
  display: flex;
  color: rgb(255, 255, 255);
}
.js-drawer-menu > ul > li:not(:last-child) {
  margin-bottom: 3.125rem;
}
.js-drawer {
  display: block;
  width: 3.125rem;
  height: 3.125rem;
  position: relative;
}
@media screen and (min-width: 768px) {
  .js-drawer {
    display: none;
  }
}

.js-drawer:hover {
  cursor: pointer;
}

.js-drawer > span {
  display: block;
  width: 3.125rem;
  padding-top: 0.3125rem;
  border-radius: 0.3125rem;
  background: rgb(0, 0, 0);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s linear;
}

.js-drawer > span::before {
  content: "";
  display: block;
  width: 3.125rem;
  padding-top: 0.3125rem;
  border-radius: 0.3125rem;
  background: rgb(0, 0, 0);
  position: absolute;
  top: 0.8125rem;
  transition: 0.3s linear;
}

.js-drawer > span::after {
  content: "";
  display: block;
  width: 3.125rem;
  padding-top: 0.3125rem;
  border-radius: 0.3125rem;
  background: rgb(0, 0, 0);
  position: absolute;
  top: -0.8125rem;
  transition: 0.3s linear;
}

.js-drawer-open > span {
  background: rgba(0, 0, 0, 0);
}
.js-drawer-open > span::before {
  transform: rotate(-45deg);
  top: 0;
}
.js-drawer-open > span::after {
  transform: rotate(45deg);
  top: 0;
}
.js-scroll-top {
  display: block;
  width: 0;
  border-top: solid 1.5625rem rgba(255, 0, 0, 0);
  border-right: solid 1.5625rem rgba(255, 0, 0, 0);
  border-bottom: solid 1.5625rem rgba(255, 0, 0, 0);
  border-left: solid 1.5625rem rgba(255, 0, 0, 0);
  border-radius: 2.8125rem;
  padding: 0;
  position: fixed;
  z-index: 10;
  bottom: 3.125rem;
  right: 3.125rem;
  cursor: pointer;
  transition: 0.3s linear;
}
.js-scroll-top::before {
  content: "top";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgba(51, 51, 51, 0);
  transition: 0.3s linear;
}

.js-scroll-top-active {
  border-top: solid 1.5625rem rgba(255, 0, 0, 0.3);
  border-right: solid 1.5625rem rgba(255, 0, 0, 0.3);
  border-bottom: solid 1.5625rem rgba(255, 0, 0, 0.3);
  border-left: solid 1.5625rem rgba(255, 0, 0, 0.3);
}

.js-scroll-top-active::before {
  color: rgba(51, 51, 51, 0.3);
}

